import { _ } from '@splunk/ui-utils/i18n';
import { isDynamicOption } from '../utils/configUtils';

export const getDynamicMajorColorEditor = ({ defaultContext, defaultPalettesConfig }) => ({
    label: _('Dynamic Major Value'),
    option: 'majorColor',
    context: 'majorColorEditorConfig',
    editor: 'editor.dynamicColor',
    showEditor: ({ options }) => isDynamicOption(options.majorColor),
    editorProps: {
        labelPosition: 'left',
        flyoutTitle: _('Dynamic Coloring: Major Value'),
        formatters: [
            {
                label: _('Ranges'),
                value: 'rangeValue',
                defaults: {
                    majorValue: defaultContext.majorColorConfig,
                },
            },
            // TODO(fkurniawan): add back in when FormatterEditor supports full MatchValue editor
            // {
            //     label: _('Matches'),
            //     value: 'matchValue',
            //     defaults: {
            //         majorValue: [{ match: 'foo', value: '#f00' }],
            //     },
            // },
        ],
        dataSelectors: [
            {
                label: _('Major Value'),
                value: 'majorValue',
            },
        ],
        defaultPalettesConfig,
    },
});
